import React from "react";
import PropTypes from "prop-types";
import { browserHistory, Link, History } from "react-router";
import { Card, Icon, Button, Table, Modal } from "antd";
import Loading from "../../../components/Loading";
import "./Child.scss";
const confirm = Modal.confirm;
const project = require("../../../../project.config");
// import './HomeView.scss'
export default class Child extends React.Component {
  static propTypes = {};
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      isHaveChild: false
    };
  }
  async componentWillMount() {
    await this.checkHavaChild();
    if (this.state.isHaveChild) {
      await this.getChildData();
    }
    await setTimeout(() => {
      this.setState({
        isLoading: false
      });
    }, 300);
  }
  checkHavaChild = async () => {
    await fetch(project.api.url + "api/Member/isAccHaveChild", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: this.props.user.authorization
      },
      body: JSON.stringify({
        Account: this.props.user.account
      })
    })
      .then(res => res.json())
      .then(
        responseJson => {
          this.setState({
            isHaveChild: responseJson
          });
        },
        function(e) {
          console.log(e);
        }
      );
  };
  getChildData = async () => {
    //å–å¾—æœ‰å“ªäº›å­¸ç«¥ï¼Œå­˜å§“åè‡³array
    await fetch(project.api.url + "api/Member/GetAccountCdName", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: this.props.user.authorization
      },
      body: JSON.stringify({
        Account: this.props.user.account
      })
    })
      .then(res => res.json())
      .then(
        responseJson => {
          var cdArray = [];
          if (responseJson.CdName.length) {
            for (var index in responseJson.CdName) {
              let cdData = {
                name: responseJson.CdName[index],
                gender: "",
                birth: "",
                avatar: ""
              };
              cdArray.push(cdData);
            }
            this.props.getChildData(cdArray);
          }
        },
        function(e) {
          console.log(e);
        }
      );
    //ä¾å–å¾—çš„å­¸ç«¥å§“åæ‹¿è©³ç´°è³‡æ–™
    var cdDetailArray = [];
    for (var index in this.props.child) {
      await fetch(project.api.url + "api/Member/CdPersonalPage", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: this.props.user.authorization
        },
        body: JSON.stringify({
          Account: this.props.user.account,
          CdName: this.props.child[index].name
        })
      })
        .then(res => res.json())
        .then(
          responseJson => {
            if (responseJson.length) {
              var cdDetailData = {
                name: responseJson[0].CdName,
                gender: responseJson[0].Gender.trim(),
                birth: responseJson[0].Birthday,
                avatar: responseJson[0].Imageurl
              };
              cdDetailArray.push(cdDetailData);
            }
          },
          function(e) {
            console.log(e);
          }
        );
      this.props.getChildData(cdDetailArray);
    }
  };

  render() {
    const editChild = async (cdName, cdBirth) => {
      sessionStorage.child_editing_name = cdName;
      sessionStorage.child_editing_birth = cdBirth;
      await browserHistory.push(project.directory + "dashboard/edit_child/");
    };
    const deleteChild = async cdName => {
      const user = this.props.user;
      confirm({
        title: "æ‚¨ç¢ºå®šåˆªé™¤æ­¤ä½å­¸ç«¥è³‡æ–™å—Žï¼Ÿ",
        content: "åˆªé™¤å¾Œå­¸ç«¥çš„æ‰€æœ‰è¨˜æ†¶åŠ›æ¸¬é‡è³‡æ–™å°‡ç„¡æ³•å¾©åŽŸ",
        okText: "ç¢ºå®š",
        okType: "danger",
        cancelText: "å–æ¶ˆ",
        onOk() {
          fetch(project.api.url + "api/Member/RmCdMember", {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
              Authorization: user.authorization
            },
            body: JSON.stringify({
              Account: user.account,
              CdName: cdName
            })
          })
            .then(res => res.json())
            .then(
              async responseJson => {
                if (responseJson.result === "success") {
                  window.location.reload();
                }
              },
              function(e) {
                console.log(e);
              }
            );
        },
        onCancel() {}
      });
    };
    const columns = [
      {
        title: "é ­åƒ",
        dataIndex: "avatar"
      },
      {
        title: "å§“å",
        className: "column-name",
        dataIndex: "name"
      },
      {
        title: "æ€§åˆ¥",
        dataIndex: "gender"
      },
      {
        title: "å‡ºç”Ÿå¹´æœˆæ—¥",
        className: "column-birth",
        dataIndex: "birth"
      },
      {
        title: "ç·¨è¼¯/åˆªé™¤",
        className: "column-edit",
        dataIndex: "edit"
      }
    ];

    var tableData = [];
    const child = this.props.child;
    if (child[0].name) {
      Object.keys(child).map(function(key, index) {
        tableData.push({
          key: index,
          avatar: (
            <div className="avatar-wrapper">
              {child[index].avatar !== "" ? (
                <img
                  className="dashboard-avatar"
                  src={
                    project.api.url +
                    "Filefolder/" +
                    child[index].avatar +
                    "?time=" +
                    new Date().getTime()
                  }
                  alt=""
                />
              ) : (
                <img
                  className="dashboard-avatar"
                  src="https://upload.wikimedia.org/wikipedia/commons/7/7c/Profile_avatar_placeholder_large.png"
                  alt=""
                />
              )}
            </div>
          ),
          name: child[index].name,
          gender: child[index].gender,
          birth: child[index].birth,
          edit: (
            <div className="link-wrapper">
              <Link
                className="child-edit-link"
                onClick={() => {
                  editChild(child[index].name, child[index].birth);
                }}
              >
                ç·¨è¼¯
              </Link>
              <Link
                className="delete-link"
                onClick={() => {
                  deleteChild(child[index].name);
                }}
              >
                åˆªé™¤
              </Link>
            </div>
          )
        });
      });
    }

    const isLoading = this.state.isLoading;
    return (
      <div>
        {isLoading && <Loading />}
        <Card
          style={{ width: "90%", marginBottom: 20 }}
          title="å­¸ç«¥è³‡æ–™"
          className="child-wrapper"
        >
          {child && (
            <Table
              columns={columns}
              dataSource={tableData}
              bordered
              pagination={false}
              className="child-table"
              locale={{ emptyText: "ç„¡æ•¸æ“š" }}
            />
          )}
        </Card>
        <div className="child-footer">
          æ†¶æƒ³å¥‡è¹Ÿæä¾›æ‚¨å¯ä»¥ç®¡ç†äº”å€‹å­©ç«¥å–”ï¼<br />
          é‚„æƒ³è®“æ›´å¤šå­©ç«¥åŠ å…¥æˆ‘å€‘å—Žï¼Ÿ å¿«ä¾†{"  "}
          <Link
            to={project.directory + "dashboard/addChild"}
            className="child-edit-link"
          >
            æ–°å¢žå­¸ç«¥
          </Link>
        </div>
      </div>
    );
  }
}



